//
//
// user-variables.scss for Dark Theme
//
// Place your own variable overrides here, these will override any Bootstrap and theme variables.

$white: #212121 ;
$gray-100: #101010 ;
$gray-200: #141414 ;
$gray-300: #474748 ;
$gray-400: #606162 ;
$gray-500: #7A7A7C ;
$gray-600: #939495 ;
$gray-700: #ADADAF ;
$gray-800: #C6C7C9 ;
$gray-900: #E0E0E3 ;
$gray-1000: #E0E0E3 ;
$gray-1100: #F9FAFD ;
$black: #fff ;

//*-----------------------------------------------
//|   Solid Colors
//-----------------------------------------------*/
$blue: #2c7be5 ;
$indigo: #727cf5 ;
$purple: #6b5eae ;
$pink: #ff679b ;
$red: #e63757 ;
$orange: #fd7e14 ;
$yellow: #f5803e ;
$green: #00d27a ;
$teal: #02a8b5 ;
$cyan: #27bcfd ;

//*-----------------------------------------------
//|   Theme Colors
//-----------------------------------------------*/
$primary: $blue ;
$secondary: $gray-600 ;
$success: $green ;
$info: $cyan ;
$warning: $yellow ;
$danger: $red ;
$light: $gray-100 ;
$dark: $gray-1100 ;

$border-color: rgba(163, 203, 255, 0.16) ;
$input-focus-border-color: $gray-400 ;
$component-active-color: #fff ;

$modal-backdrop-bg: $white ;
$modal-backdrop-opacity: 0.7 ;

$navbar-dark-color: rgba($black, 0.5) ;
$navbar-dark-hover-color: rgba($black, 0.75) ;
$navbar-dark-active-color: $black ;
$navbar-dark-disabled-color: rgba($black, 0.25) ;

$table-head-bg: $white ;

$table-hover-bg: $gray-200 ;
$table-dark-color: $black ;
$table-dark-bg: $white ;
$table-dark-accent-bg: rgba($white, 0.05) ;
$table-dark-border-color: darken($table-dark-bg, 7.5%) ;
